.App {
  text-align: center;
}

h1 {
  font-size: 36px;
  margin-top: 50px;
}


.number {
  display: inline-block;
  font-size: 30px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgb(55, 214, 206);
  color: white;
  text-align: center;
  line-height: 60px;
  margin-right: 10px;
  margin-left: 10px;
}

.nc {
  margin-top: 30px;
  font-size: 50px;

  background-color: #0a0a23;
  color: #fff;
  border: none;
  border-radius: 10px;
}

a {
  color: blue;
  font-size: 30px;
  text-decoration-line: none;
}

.toss {
  float: right;
  background-color: #0a0a23;
  border-radius: 10px;
  text-align: center;
  margin-top: 40vh;
  margin-left: -10vh;
  position: absolute;

}